import React from 'react';
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import Range from '../components/Range';
import Output from '../components/Output';
import '../sass/index.sass';
import '../sass/Bmi.sass';

class Bmi extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      height:170,
      weight:65,
      bmi: 22.49,
      bmiClass: 'В пределах нормы'
    }
  }

  heightChange(height){
    this.setState({height:height}, this.setBmi);
  }

  weightChange(weight){
    this.setState({weight:weight}, this.setBmi);
  }

  setBmi(){
    let bmi = ((this.state.weight / this.state.height / this.state.height) * 10000).toFixed(2);
    this.setState({bmi: bmi, bmiClass: this.getBmiClass(bmi)}, function(){
      //console.log(this.state);
    });
  }

  getBmiClass(bmi){
    if(bmi < 18.5) return 'Недовес';
    if(bmi >= 18.5 && bmi <= 24.9) return 'В пределах нормы';
    if(bmi >= 25 && bmi <= 29.9) return 'Начало ожирения';
    if(bmi >= 30) return 'Ожирение';
  }

  render() {
    return (
      <div className="bmi-page">
      <Helmet title={`Калькулятор BMI`} />
      <Link className="btn-back" to="/">На главную</Link>
        <div className="App">
          <h2>Индекс массы тела</h2>
          <form>
            <div>
              <label>Рост</label>
              <Range value={this.state.height} onChange={this.heightChange.bind(this)} />
            </div>
            <div>
              <label>Вес</label>
              <Range value={this.state.weight} onChange={this.weightChange.bind(this)} />
            </div>
          </form>
          <Output data={this.state} />
        </div>
      </div>
    );
  }
}

export default Bmi;



