import React, { Component } from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

class Output extends Component {
  render() {
    let height = this.props.data.height
    let weight = this.props.data.weight
    let bmi = this.props.data.bmi
    let bmiClass = this.props.data.bmiClass

    return (
      <div className="output">
        <h3>{height} см</h3>
        <h3>{weight} кг</h3>
        <h3>{bmi}</h3>
        <h3 className={this.props.data.bmiClass === 'Ожирение' ? 'Опасность' : ''}>
          {bmiClass}{' '}
          {this.props.data.bmiClass === 'Ожирение' ? (
            <Link to="/bodypart/cardio">Что можно сделать?</Link>) : ('')}
        </h3>
      </div>
    )
  }
}

export default Output



